import React, { Component } from 'react';
import { Button } from 'antd';
import './style.scss';

export default class BtnDelete extends Component {
  states = '';

  render() {
    return (
      <Button
        {...this.props}
        className={`btnAction btnDelete ${this.props.className}`}
        icon="delete"
        size="small"
      >
        {this.props.title}
      </Button>
    );
  }
}

BtnDelete.defaultProps = {
  className: '',
  title: '',
};



