import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import BtnAddNew from 'components/common/button/BtnAddNew';
import BtnEdit from 'components/common/button/BtnEdit';
import BtnDelete from 'components/common/button/BtnDelete';
import { Card, Row, Table, Popconfirm, Col, message } from 'antd';
import { getPrizeClassification, deletePrizeClassification } from 'services/prizeClassification';

class ListSholarshipRatingsPage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      listPrizeClassification: [],
      loading: true,
    };
    this.columns = [
      {
        title: 'Mã xếp loại',
        dataIndex: 'id',
        key: 'id',
      },
      {
        title: 'Tên xếp loại',
        dataIndex: 'label',
        key: 'label',
      },
      {
        title: 'Từ điểm HT 10',
        dataIndex: 'gradeIn10',
        key: 'gradeIn10',
      },
      {
        title: 'Từ điểm HT 4',
        dataIndex: 'gradeIn4',
        key: 'gradeIn4',
      },
      {
        title: 'Từ điểm RL',
        dataIndex: 'gradeInPractice',
        key: 'gradeInPractice',
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        render: (text, record) =>
          this.state.listPrizeClassification.length >= 1 ? (
            <Fragment>
              <BtnEdit href={`#scholarshipRatings/form/edit/${record.id}`} />
              <Popconfirm title="Sure to delete?" onConfirm={() => this.handleDelete(record.id)}>
                <BtnDelete />
              </Popconfirm>
            </Fragment>
          ) : null,
      },
    ];
  }

  componentDidMount = async () => {
    this.getEthnicity();
  };

  getEthnicity = async () => {
    const res = await getPrizeClassification();
    this.setState({ listPrizeClassification: res.data.items, loading: false });
  };

  handleDelete = id => {
    deletePrizeClassification(id).then(res => {
      if (res.status === 200) {
        message.success('Xóa học bổng thành công.');
        this.getEthnicity();
      } else {
        message.error(res.message);
      }
    });
  };

  render() {
    return (
      <Card title="Xếp loại học bổng">
        <Row>
          <Col span={24}>
            <BtnAddNew href="#/scholarshipRatings/form/new" />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <Table
              loading={this.state.loading}
              bordered
              dataSource={this.state.listPrizeClassification}
              columns={this.columns}
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ListSholarshipRatingsPage />
  </Page>
);



